import React from "react";
import { Link } from "react-router-dom";
import "./SocialsLogIn.scss";

export function SocialsLogIn({ onLogInWithEmailClick }) {
  return (
    <div className="socialsLogIn__container">
      <div className="socialsLogIn__content_socials">
        <a
          href="http://facebook.com"
          className="socialsLogIn__content_socials_link"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            xlink="http://www.w3.org/1999/xlink"
            width="312px"
            height="48px"
          >
            <image
              x="0px"
              y="0px"
              width="312px"
              height="48px"
              href="data:img/png;base64,"
            />
          </svg>
        </a>
        <a
          href="http://google.com"
          className="socialsLogIn__content_socials_link"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            xlink="http://www.w3.org/1999/xlink"
            width="316px"
            height="48px"
          >
            <image
              x="0px"
              y="0px"
              width="316px"
              height="48px"
              href="data:img/png;base64,"
            />
          </svg>
        </a>
      </div>
      <div className="socialsLogIn__content_button-cover">
        <button
          onClick={onLogInWithEmailClick}
          className="socialsLogIn__content_emailLogIn-btn"
        >
          Log in with Email
        </button>
      </div>
    </div>
  );
}
