import React from "react";
import { Link } from "react-router-dom";
import "./SocialsSignUp.scss";

export function SocialsSignUp({ onSignUpWithEmailClick }) {
  return (
    <div className="socialsSignUp__conteiner">
      <div className="socialsSignUp__content_socials">
        <a
          href="http://facebook.com"
          className="socialsSignUp__content_socials_link"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            xlink="http://www.w3.org/1999/xlink"
            width="317px"
            height="48px"
          >
            <image
              x="0px"
              y="0px"
              width="312px"
              height="48px"
              href="data:img/png;base64,"
            />
          </svg>
        </a>
        <a
          href="http://google.com"
          className="socialsSignUp__content_socials_link"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            xlink="http://www.w3.org/1999/xlink"
            width="316px"
            height="48px"
          >
            <image
              x="0px"
              y="0px"
              width="312px"
              height="48px"
              href="data:img/png;base64,"
            />
          </svg>
        </a>
      </div>
      <div className="socialsSignUp__content_button-cover">
        <button
          onClick={onSignUpWithEmailClick}
          className="socialsSignUp__content_emailSignUp-btn"
        >
          Sign up with Email
        </button>
      </div>
    </div>
  );
}
